import { OnInit, OnDestroy, Component } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SocketIoService } from './services/socket-io.service';
import { RegistroMDC } from './models/registro.interface'
import { Status } from './models/typesStatus.enum';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();
  public title: string = 'client-show-users';
  public user: RegistroMDC = {};
  public dirStatus = Status;
  public statusAlert = Status.WAITING;
  public date: Date;
  public time: Date;
  private intervalId;
  private subscription: Subscription;
  private isShowingUser = false;


  constructor(
    private router: Router,
    private socketService: SocketIoService,
  ) {

  }

  ngOnInit() {
    this.clearUser();
    this.initTimer();
    this.subscribeToSockets();
    console.log('sdgfsdfsdf', window.location.hostname)
  }

  ngOnDestroy() {
    clearInterval(this.intervalId);
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  clearUser() {
    this.statusAlert = Status.WAITING;
    this.isShowingUser = false;
    this.user = {
      imgProfileUrl: "assets/img/noPhoto.png",
      listaMensajes: 'Esperando Lectura',
      nombre: '',
      puerta: '',
      workarea: '',
      contractor: '',
      department: '',
    }
  }

  getStatusAlert(tipoAlerta: number): Status {
    switch (tipoAlerta) {
      case 1: return Status.OK;
      case 2: return Status.WARNING;
      case 3: return Status.DANGER
      default: return Status.WAITING;
    }
  }

  initTimer(): void {
    this.intervalId = setInterval(() => {
      this.time = new Date();
      this.date = new Date();
    }, 1000);
  }

  setUser(registroMDC: RegistroMDC): void {
    if (registroMDC.nombre !== '' && !this.isShowingUser) {
      this.isShowingUser = true;
      this.statusAlert = this.getStatusAlert(registroMDC.tipoAlerta);
      this.user.imgProfileUrl = registroMDC.imgProfileUrl != null ? registroMDC.imgProfileUrl : 'assets/img/noPhoto.png';
      this.user.nombre = registroMDC.nombre;
      this.user.puerta = registroMDC.puerta;
      this.user.listaMensajes = registroMDC.listaMensajes;
      this.user.workarea = registroMDC.workarea;
      this.user.contractor = registroMDC.contractor;
      this.user.department = registroMDC.department;      
      setTimeout(() => { this.clearUser(); }, 6000);
    }
  }

  subscribeToSockets(): void {
    this.socketService.onListenerRegistroMdc()
      .pipe(takeUntil(this.destroy$))
      .subscribe(resp => {
        this.setUser(resp);
        console.log(resp)
      })
  }

}
